input {
  display: inline-block;
  width: 200px;
  height: 50px;
  font: inherit;
  font-size: 20px;
  border: none;
  outline: none;
  width: 100%;
  padding-left: 4px;
  padding-right: 4px;
}
.movie__form {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 600px;
  background-color: #fff;
  border-radius: 3px;
  overflow: hidden;
}
.SearchForm__button {
  border: none;
  background-color: #fff;
  padding: 10px;
}
.searchbar {
  top: 0;
  left: 0;
  position: sticky;
  z-index: 1100;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 12px;
  padding-bottom: 12px;
  color: #fff;
  background-color: #efef76;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}
